import React from 'react';

const ProtectionDetailListItem = props => {
    const { extra, children } = props;
    return (
        <div className="protection-detail-listItem">
            <div className="protection-detail-listItem-label">{children}</div>
            <div className="protection-detail-listItem-extra">{extra}</div>
        </div>
    );
};

export default ProtectionDetailListItem;
