import React from "react";
import Item from "@/pages/protection/detail/List/Item";
require("./style.scss");

export default class ProtectionDetailList extends React.Component {
  props: any;
  static item = Item;
  constructor(props) {
    super(props);
  }

  render() {
    const { children } = this.props;
    return <div className="protection-detail-list">{children}</div>;
  }
}
