import React, { useEffect } from "react";
import { observer } from "mobx-react";
import { Icon } from "antd-mobile";
import dayjs from "dayjs";
import SubPage from "@/components/SubPage";
import StarSwitch from "@/components/StarSwitch";
import { DEFENSE_STATUS } from "../constant";
import List from "./List";
import storage from "@/utils/storage";

const Detail = observer((props: any) => {
  const { store, params, history } = props;
  const { detail, reset, addDeviceFollow, deleteDeviceFollow } = store;

  useEffect(() => {
    store.fetchDefenseAreaDetail(params.id);
    return () => {
      reset();
    };
  }, []);

  const followChange = () => {
    if (detail.followId) {
      deleteDeviceFollow(detail.id, detail.followId);
    } else {
      addDeviceFollow(detail.id);
    }
  };

  const renderItem = () => {
    const { detail } = store;

    return (
      <List>
        <List.item extra={DEFENSE_STATUS[detail.status] || "无"}>布防状态</List.item>
        <List.item extra={detail.mainFrameName || "无"}>所属主机</List.item>
        <List.item extra={detail.areaLocationName || "无"}>位置信息</List.item>
        <List.item extra={detail.orgName || "无"}>机构名称</List.item>
        <List.item extra={`${detail.memberName || ""} ${detail.phone || ""}`}>负责人</List.item>
        <List.item extra={detail.statusTime && dayjs(detail.statusTime).format("YYYY-MM-DD HH:mm:ss")}>最近布防时间</List.item>
      </List>
    );
  };

  return (
    <SubPage history={history} title="防区详情" bgColor="#fff">
      {detail && detail.id ? (
        <>
          <div className="protection-detail-header" onClick={followChange}>
            <span className="protection-detail-header-title">{detail.name}</span>
            <span className="protection-detail-header-state">
              {detail.followId && detail.user_id === storage.getItem("userId") ? <StarSwitch on /> : <StarSwitch />}
            </span>
          </div>
          {renderItem()}
        </>
      ) : (
        <div className="protection-detail-loading">
          <Icon type="loading" />
        </div>
      )}
    </SubPage>
  );
});

export default Detail;
