import React from 'react';
import classNames from 'classnames';
import off from '@/assets/imgs/protection/protection_bt_collection_off.png';
import on from '@/assets/imgs/protection/protection_bt_collection_on.png';
require('./style.scss');

const StarSwitch = (props: any) => (
    <img
        className={classNames('starSwitch-img', props.className)}
        src={props.on ? on : off}
    />
);

export default StarSwitch;
