import { observable, action } from "mobx";
import { toJS } from "mobx";
import DataApiService from "@/service/http";
import { Toast } from "antd-mobile";

class Store {
  @observable modules: any = undefined;
  // @observable state: any = EnumState['noop'];
  @observable children: any = null;
  @observable error: any = undefined;
  @observable photo: any = undefined;

  @observable height: string = "20%";
  @observable response: any = {};
  @observable detail: any = {};
  @observable device_id: string = "";
  @observable following: boolean = false;

  @action reset = () => {
    this.detail = {};
  };

  @action
  fetchDefenseAreaDetail(id: string) {
    DataApiService.getDetailPageData({
      tableInfo: [
        {
          tableName: "view_defense_area_information",
          tableType: 1,
          dataColumns: [
            { columnName: "areaLocationName" },
            { columnName: "orgName" },
            { columnName: "groupId" },
            { columnName: "memberName" },
            { columnName: "buildId" },
            { columnName: "floorId" },
            { columnName: "followId" },
            { columnName: "areaId" },
            { columnName: "phone" },
            { columnName: "statusTime" },
            { columnName: "name" },
            { columnName: "id" },
            { columnName: "mainFrameName" },
            { columnName: "memberId" },
            { columnName: "status" },
            { columnName: "user_id" }
          ],
          filterCondition: [
            {
              field: "id",
              type: 3,
              value: id
            }
          ]
        }
      ]
    })
      .then(res => {
        const { result, success } = res;
        if (success && result.dataJson && result.dataJson.view_defense_area_information) {
          this.detail = result.dataJson.view_defense_area_information[0];
        } else {
          this.detail = {};
        }
      })
      .catch(err => {
        this.detail = {};
      });
  }

  @action.bound
  addDeviceFollow(device_id: string) {
    if (this.following) return;
    this.following = true;
    DataApiService.addDeviceFollow({
      device_id
    })
      .then(res => {
        Toast.info("关注成功", 1);
        this.response = res;
      })
      .finally(() => {
        this.fetchDefenseAreaDetail(device_id);
        this.following = false;
      });
  }

  @action.bound
  deleteDeviceFollow(device_id: string, followId: string) {
    if (this.following) return;
    this.following = true;
    DataApiService.deleteDeviceFollow({ ids: [followId] })
      .then(res => {
        Toast.info("取消成功", 1);
        this.response = res;
      })
      .finally(() => {
        this.fetchDefenseAreaDetail(device_id);
        this.following = false;
      });
  }
}

export default new Store();
